/*
输入 ?id=123&name=bbbb
输出 {id:123,name:bbbb}
 */
function urlParse() {
	var url = window.location.search; //获取url里面 ？后面的参数
	var obj = {};
	var reg = /[?&][^?&]+=[^?&]/g; //匹配？&符号后面的一个或者多个非？&的用=等于号链接的字符
	var arr = url.match(reg);
	if (arr) {
		arr.forEach(function (item) {
			var temparr = item.substring(1).split("=");
			var key = decodeURIComponent(temparr[0]);
			var val = decodeURIComponent(temparr[1]);
			obj[key] = val;
			//console.log(key);
			//console.log(val);
		});
	}
	return obj;
}
